.bigimg{
    background-image: url('../../../images/forestbridge.jpg');
    height: 85vh;
    background-position: center;
    background-size: cover;
    position: relative;
    color: #ffffff;
    font-family: 'Courier New', Courier, monospace;
    font-size: 25px;
}

.topleft{
    position: absolute;
    top: 0;
    left: 16px;
}

.bottomleft{
    position: absolute;
    bottom: 0;
    left: 16px;
}

.middle{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
}

hr{
    margin: auto;
}
